body {
  /* background-color: cadetblue; */
  font-family: Roboto;
  /* touch-action: none; */
}

/* not required - in SlipMove instead */
.list {
  position: relative; /* Required by react-flip-move - uncomment it to see console warning */
  /* touch-action:none; */
}


.blank {
  position: relative; /* ensures that item is above all others in chrome */
  height: 50px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;  
  background-color: transparent;
  border: 1px solid rgba(0,0,0,0);
  margin-bottom: 2px;
  margin-top: 2px;
  user-select: none;
  will-change: transform;
}



.list-item {
  position: relative; /* ensures that item is above all others in chrome */
  height: 50px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;  
  background-color: transparent;
  border: 1px solid rgba(0,0,0,.125);
  margin-bottom: 2px;
  margin-top: 2px;
  user-select: none;
  will-change: transform;
}

.placeholder {
  position: relative; /* ensures that item is above all others in chrome */
  height: 50px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;  
  background-color: #0001;
  border: 1px solid rgba(0,0,0,.125);
  margin-bottom: 2px;
  margin-top: 2px;
  user-select: none;
  will-change: transform;
  /* pointer-events: none; */
}

.slipmove-dragging {
  background-color: orange;
}

